.MuiContainer-root h2 {
  text-align: center;
}

.MuiCardContent-root,
.userOutput {
  display: flex;
  flex-direction: column;
}
.MuiCardContent-root {
  margin: 10px;
}
.userOutput {
  margin-top: 30px;
  .output_perc .MuiFormControl-root {
    width: 80px;
  }
  .output_income .MuiFormControl-root {
    min-width: 80px;
  }
  .output_perc,
  .output_income {
    display: flex;
    align-items: baseline;
    input {
      text-align: center;
      font-family: inherit !important;
    }
    span {
      font-size: 1.1em;
    }
  }
  .MuiButton-root {
    width: 200px;
  }
}

.MuiFormControl-root {
  margin-bottom: 10px !important;
}
.MuiFormLabel-root.MuiInputLabel-shrink {
  font-size: 1.2rem;
}
.MuiFormHelperText-root {
  margin-top: 0 !important;
}

.MuiButton-root {
  margin-top: 30px !important;
}
